/*Reset Site Styles*/

a {
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
}

a:hover {
  font-weight: bold;
  text-decoration: underline;
}

/*Module Styles*/

.Main {
  text-align: center;
  align-content: center;
  color: #fff;
}

.Main-filter-text {
  font-weight: bold;
  font-size: 1rem;
}

.Main-filter-platforms,
.Main-filter-years,
.Main-filter-genres,
.Main-filter-reviews,
.Main-filter-playtimes,
.Main-filter-ages {
  border-radius: 15px;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
  padding: 2px;
  height: 1.5rem;
}

.Main-filter {
  justify-content: space-around;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(7, minmax(0.5, 1fr));
  grid-auto-flow: column;
}

.Main-table-header {
  display: grid;
  width: 100%;
  margin: 0 auto;
  grid-template-columns: repeat(7, minmax(0, 1fr));
  gap: auto;
  grid-auto-flow: column;
  background-color: #364563;
  border: solid 2px #202a40;
}

.Main-table-row {
  display: grid;
  width: 100%;
  margin: 0 auto;
  grid-template-columns: repeat(7, minmax(0, 1fr));
  gap: auto;
  grid-auto-flow: row;
}

.Main-table-header-titles,
.Main-table-header-platforms,
.Main-table-header-years,
.Main-table-header-genres,
.Main-table-header-reviews,
.Main-table-header-playtimes,
.Main-table-header-ages {
  font-weight: bold;
  font-size: 1rem;
  padding: 2px;
  height: 1.5rem;
}

.Main-table-row-titles,
.Main-table-row-platforms,
.Main-table-row-years,
.Main-table-row-genres,
.Main-table-row-reviews,
.Main-table-row-playtimes,
.Main-table-row-ages {
  align-content: center;
  font-size: 1rem;
  padding: 2px;
  height: 100%;
  border-bottom: solid 2px #202a40;
}

@media (max-width: 991px) {
  .Main-filter {
    grid-template-areas:
      "Main-filter-text . ."
      "Main-filter-platforms Main-filter-years Main-filter-genres"
      "Main-filter-reviews Main-filter-playtimes Main-filter-ages";
    grid-template-rows: auto;
    gap: 15px;
    grid-auto-flow: row;
  }

  .Main-table-header {
    min-height: 3rem;
    gap: 3px;
    word-wrap: break-word;
  }

  .Main-table-row-titles,
  .Main-table-row-platforms,
  .Main-table-row-years,
  .Main-table-row-genres,
  .Main-table-row-reviews,
  .Main-table-row-playtimes,
  .Main-table-row-ages {
    gap: 3px;
    word-wrap: break-word;
  }

  .Main-filter-text {
    grid-column: 1 / 4;
  }
}
